import { useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import Layout from '../../components/Layout';
import Sidebar from './sidebar';
import { useSelector, useDispatch } from 'react-redux';
import { selectUser, selectCustomers, getCustomerByKeyword } from '../../redux/userReducer';
import { selectClassProducts, getProductsByKeyword } from '../../redux/productReducer';
import { selectClassCategories, getCategoryByKeyword } from '../../redux/pcategoryReducer';
import { addCurOrders, setCustomer } from '../../redux/orderReducer';
import { API_IMAGE_PATH } from '../../config/constants';

const Search = () => {
  const { filter, keyword } = useParams();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const customers = useSelector(selectCustomers);
  const products = useSelector(selectClassProducts);
  const categories = useSelector(selectClassCategories);
  const seluser = useSelector(selectUser);

  useEffect(() => {
    if (filter === 'customer') {
      dispatch(getCustomerByKeyword(keyword));
    } else if (filter === 'category') {
      dispatch(getCategoryByKeyword(keyword));
    } else {
      dispatch(getProductsByKeyword(keyword));
    }
  }, [filter, keyword]);

  const handleProduct = (p) => {
    const order = {
      productId: p._id,
      amount: 1
    }
    dispatch(addCurOrders(order));
  }

  const handleCustomer = (c) => {
    dispatch(setCustomer(c));
  }

  const handleCategory = (id) => {
    navigate(`/details/${id}`);
  }

  return (
    <Layout classes={'bg-[#404040] h-full'} searchBar={false}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-80px)]">
              <div className='text-center text-xl text-gray-100 font-bold'>
                {/* {`The search result with "${keyword}" of ${filter.toUpperCase()}`} */}
                {`Search results for  ${filter.toUpperCase()}`}
              </div>
              {filter === 'customer' && <div>
                {
                  customers.map((c, index) => {
                    return (
                      <div key={index}>
                        <div 
                          className='w-full flex text-gray-100 px-[1rem] leading-[1.8rem] py-[0.5rem] cursor-pointer'
                          onClick={() =>handleCustomer(c)}
                        >
                          <p className='w-[20%] text-center'>
                            {index + 1}  
                          </p>
                          <p className='w-[80%] text-center text-xl font-bold'>{c.username ? c.username : c.businessname}</p>                          
                        </div>
                      </div>
                    );
                  })
                }
              </div>}
              {filter === 'product' && <div className='grid grid-cols-3 gap-6 px-[2rem] py-[2rem]'>
                  {
                      products.map((product, index) => {
                          return (
                              <div 
                                  key={index} 
                                  className='w-full h-[9rem] flex flex-row text-xl rounded-[0.6rem] bg-[#D9D9D9] text-gray-800 select-none cursor-pointer'
                                  onClick={() => handleProduct(product)}
                              >
                                  <div className='w-[50%] flex flex-col text-center justify-center items-center'>
                                    <h1 className='font-bold'>{product.name}</h1>
                                    <p className='text-md'>{product.description}</p>                                  
                                    <p>Â£{product.price.toFixed(2)}</p>
                                  </div>                  
                                  <div className='w-[50%] flex flex-col text-center justify-center items-center p-[1rem]'>                
                                    <img 
                                      src={`${API_IMAGE_PATH}/${product.photo ? product.photo : "default.png"}`} 
                                      className='border border-gray-700 w-full h-full my-2 object-fill object-center'
                                    />
                                  </div>
                              </div>
                          )
                      })
                  }
              </div>}
              {filter === 'category' && <div className='grid grid-cols-3 gap-6 px-[2rem] py-[2rem]'>
                  {
                      categories.map((category, index) => {
                          return (
                              <div 
                                  key={index} 
                                  className='w-full h-[6rem] text-xl text-center leading-[6rem] rounded-[0.6rem] bg-[#D9D9D9] text-gray-800 select-none cursor-pointer'
                                  onClick={() => handleCategory(category._id)}
                              >
                                  {category.name}
                              </div>
                          )
                      })
                  }
              </div>}
            </div>
            {seluser.role === 'merchant' && <Sidebar />}
        </div>        
    </Layout>
  );
}

export default Search;